.profile__image {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    display: flex;
    position: relative;
    overflow: hidden;
    background-color: transparent;
    padding: 0;
    border: 0;
}

.profile__image::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
  background-image: url(../../../images/vector_pencil.svg);
  background-size: 26px 26px;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
}

