    height: calc(120px + (273 - 120)*((100vw - 320px)/(728 - 320px)));
    margin-right: 0;
    margin-bottom: 26px;
  }
} */

@media (hover: hover) and (pointer: fine) {
  .profile__image:hover::before {
    opacity: 1;
    transition: opacity .3s linear;
    cursor: pointer;
  }
}
